.userTest {
  //border: 1px solid white;
  padding: 0 20px;
  display: flex;
  justify-content: space-between;
  flex-direction: column;

  .top {
    @include breakpoint($large) {
      flex-direction: column;
    }
    display: flex;
    justify-content: space-between;

    img {
      @include breakpoint($large) {
        flex: 1 1 100%;
        width: 100%;
      }
      flex: 0 1 auto;
      width: 272px;
      height: auto;
    }

    .main {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;

      p {
        color: #fff;
      }

      .starBox {
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        margin-bottom: 10px;

        p {
          color: #fff;
          margin-bottom: 0;
          padding-right: 10px;
        }

        span {
          background: linear-gradient(
            180deg,
            #fdf8a2 3%,
            #f4d97a 47%,
            #eec462 100%
          );
          -webkit-background-clip: text;
          -webkit-text-fill-color: transparent;
          font-size: 20px;
          margin-right: 3px;
        }
      }
    }
  }

  .paragraph {
    @include breakpoint($ipad) {
      margin-top: 0;
      font-size: 1rem;
      line-height: 1.8;
    }
    margin-top: 30px;
    font-size: 18px;
    color: #ffffff;
    letter-spacing: 0;
    line-height: 38px;
    font-weight: 300;
  }

  &_title {
    font-weight: 100;
    font-size: 2.125rem;
    color: #fff;
  }
}
.userFeedBackBox {
  overflow-y: scroll;
}
.userBox {
  padding: 18px 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: rgba(0, 0, 0, 0.65);
  margin-bottom: 8px;

  .userNumber {
    text-align: center;
    flex: 1 1 100%;
    padding: 5px;
    width: 100%;
    border: 1px solid white;
    color: #fff;
    font-size: 1.125rem;
  }
  .starBox {
    margin: 10px;
    span {
      color: #f70000;
      font-size: 20px;
      margin: 0 2px;
    }
  }
  p {
    color: #fff;
    font-weight: 300;
    letter-spacing: 0;
    line-height: 24px;
    margin-bottom: 0;
  }
}
