.BottomNav {
  a {
    &:active {
      color: #ffcf4c;
    }
    &:hover {
      color: #ffcf4c;
    }
    span {
      &:hover {
        background: linear-gradient(
          180deg,
          #fdf8a2 3%,
          #f4d97a 47%,
          #eec462 100%
        );
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
      }
      line-height: 30px;
      color: inherit;
      font-size: 1.8rem;
    }
    text-decoration: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 12px;
    color: #fff;
  }
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  bottom: 0;
  height: auto;
  background-image: linear-gradient(
    270deg,
    #402f80 0%,
    #472265 33%,
    #1e1549 77%
  );
}
