.navBarFlex {
  span {
    background-image: linear-gradient(
      180deg,
      #fdf8a2 3%,
      #f4d97a 47%,
      #eec462 100%
    );
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  background-image: linear-gradient(
    270deg,
    #402f80 0%,
    #472265 33%,
    #1e1549 77%
  );
  width: 100%;
  padding: 8px 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  .title {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    color: $mainText;
  }
  .logo {
    &isUser {
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
    }
    position: absolute;
    left: 50%;
    transform: translateX(-80%);
  }
  .btnGroup {
    display: flex;
    max-width: 96px;
    width: 100%;
    justify-content: space-between;
    .btnOutLine {
      border: 1px solid $lightYellow;
      padding: 2px 8px;
      border-radius: 4px;
      white-space: nowrap;
      font-size: 0.85rem;
      color: $lightYellow;
      background-color: unset;
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }
}
